export default {
  data() {
    return {
      passwordInput: {
        isShow: false,
        type: "password"
      },
      confirmPasswordInput: {
        isShow: false,
        type: "password"
      }
    }
  },
  methods: {
    togglePasswordVisibility(passwordInput) {
      if (passwordInput.type === "password") {
        passwordInput.type = "text";
        passwordInput.isShow = true;
      } else {
        passwordInput.type = "password";
        passwordInput.isShow = false;
      }
    }
  }
}
